<script>
import VideoBackground from "vue-responsive-video-background-player";
import { gsap } from "gsap";
import { ScrambleTextPlugin } from "gsap/ScrambleTextPlugin";
import { TextPlugin } from "gsap/TextPlugin";

export default {
  components: {
    VideoBackground,
  },
  data() {
    return {
      isVideoPlaying: true,
      showModal: false,
    };
  },
  methods: {
    skipAnimation() {
      this.isVideoPlaying = !this.isVideoPlaying;
    },
    isShowModal() {
      const modal = document.getElementsByClassName("modal-mask")[0];
      if (!this.showModal) {
        this.showModal = true;
        modal.classList.add("active");
      } else {
        this.showModal = false;
        modal.classList.remove("active");
      }
    },
    scrambleText() {
      let tl = gsap.timeline({ defaults: { duration: 1, ease: "none" } });
      tl.to("#gxScrambleOne", {
        duration: 0.5,
        scrambleText: {
          text: "Your ultimate data platform",
          chars: "lowerCase",
          revealDelay: 0,
          tweenLength: false,
        },
      })
        .to("#gxScrambleTwo", {
          duration: 0.5,
          scrambleText: {
            text: "AI-driven Smart Data Platform turning data into truth",
            chars: "lowerCase",
            revealDelay: 0.5,
            tweenLength: false,
          },
        })
        .to("#gxScrambleThree", {
          duration: 0.5,
          text: "The ontology integrates data from multiple sources into a single ecosystem and provides analytics teams with high quality fuel. The Analytics engine offers insight advantage through early trend detection. The Visualisation layer offers beautifully engineered dashboards with real-time simulation.",
          opacity: 1,
        })
        .to("#gxScrambleFour", {
          duration: 0.5,
          opacity: 1,
          text: "Let’s see what this thing can do.",
        })
        .to("#gxScrambleFive", {
          duration: 0.5,
          opacity: 1,
        });
    },
    allowScroll() {
      document.body.classList.toggle("scrollLocked");
    },
  },
  mounted() {
    gsap.registerPlugin(ScrambleTextPlugin, TextPlugin);
    document.body.classList.add("scrollLocked");
    const homeVideo = document
      .getElementById("videoHome")
      .querySelectorAll("video")[0];
    const introVideo = document.getElementsByClassName("intro-video")[0];

    homeVideo.addEventListener("canplaythrough", function () {
      setTimeout(function () {
        if (!introVideo.classList.contains("hidden")) {
          introVideo.classList.add("hidden");
          scrambleText();
          allowScroll();
        }
      }, 13000);
    });

    function scrambleText() {
      let tl = gsap.timeline({ defaults: { duration: 1, ease: "none" } });
      tl.to("#gxScrambleOne", {
        duration: 0.5,
        scrambleText: {
          text: "Your ultimate data platform",
          chars: "lowerCase",
          revealDelay: 0,
          tweenLength: false,
        },
      })
        .to("#gxScrambleTwo", {
          duration: 0.5,
          scrambleText: {
            text: "AI-driven Smart Data Platform turning data into truth",
            chars: "lowerCase",
            revealDelay: 0.5,
            tweenLength: false,
          },
        })
        .to("#gxScrambleThree", {
          duration: 0.5,
          text: "The ontology integrates data from multiple sources into a single ecosystem and provides analytics teams with high quality fuel. The Analytics engine offers insight advantage through early trend detection. The Visualisation layer offers beautifully engineered dashboards with real-time simulation.",
          opacity: 1,
        })
        .to("#gxScrambleFour", {
          duration: 0.5,
          opacity: 1,
          text: "Let’s see what this thing can do.",
        })
        .to("#gxScrambleFive", {
          duration: 0.5,
          opacity: 1,
        });
    }
    function allowScroll() {
      document.body.classList.toggle("scrollLocked");
    }
  },
  watch: {
    isVideoPlaying() {
      const introVideo = document.getElementsByClassName("intro-video")[0];
      if (!introVideo.classList.contains("hidden")) {
        introVideo.classList.add("hidden");
        this.scrambleText();
        this.allowScroll();
      }
    },
  },
};
</script>

<style lang="scss">
.intro-video {
  opacity: 1;
  transition: opacity 1s ease;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-width: 100%;
  min-height: 100%;
  &.hidden {
    opacity: 0;
    transition: opacity 1s ease;
    pointer-events: none;
  }
}
.skip {
  position: absolute !important;
  left: 0;
  right: 0;
  bottom: 50px;
  color: $white;
  cursor: pointer;
  justify-content: center;
  z-index: 2;
  @include breakpoint($medium) {
    right: 100px;
    left: auto;
    text-align: left;
  }
}
.gx-logo {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-content: center;
  position: absolute;
  z-index: 2;
  left: 0;
