  margin: 0 auto;
  animation: goTransparent 1s forwards;
  animation-delay: 2s;
  pointer-events: none;
  svg {
    width: 80vw;
    @include breakpoint($large) {
      width: 50vw;
    }
  }
  #eSGJivPWXx42 {
    animation: eSGJivPWXx42_c_o 2.5ms linear 1 normal forwards;
  }
  @keyframes goTransparent {
    0% {
