    0% {
      opacity: 0;
    }
    9.090909% {
      opacity: 1;
    }
    100% {
      opacity: 1;
    }
  }
}

.gx-section--one {
  text-align: center;
  background: radial-gradient(
    at -8% 100%,
    rgba(3, 77, 185, 0.4) 5px,
    transparent 70%
  );
  background-repeat: no-repeat;
  background-position: bottom left;
  color: $white;
  display: flex;
  align-content: center;
  justify-content: center;
  flex-direction: column;
  overflow: hidden;
  .content {
    width: 100%;
    margin: 0 auto;
    @include breakpoint($medium) {
      width: 70%;
    }
    h1 {
      margin-bottom: 3rem;
      @include breakpoint($medium) {
        margin-bottom: 5vh;
      }
    }
    h4 {
      margin-bottom: 1rem;
      @include breakpoint($medium) {
        margin-bottom: 2vh;
      }
    }
    h5 {
      @include breakpoint($medium) {
        max-width: 70%;
        margin-bottom: 2.5rem;
      }
      margin-left: auto;
      margin-right: auto;
      &:last-child {
        margin-bottom: 0;
      }
    }
    button {
      margin-top: 3rem;
      opacity: 0;
      @include breakpoint($large) {
        margin-top: 5vh;
      }
    }
  }
}
.modal-mask {
  position: fixed;
  z-index: 99;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-content: center;
  transition: opacity 0.3s ease;
  pointer-events: none;
  opacity: 0;
  &.active {
    opacity: 1;
    transition: opacity 0.3s ease;
    pointer-events: all;
    .modal-container {
      transform: scale(1);
      transition: all 0.3s ease;
    }
  }
  .modal-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .modal-container {
    width: 100vw;
    height: auto;
    @include breakpoint($large) {
      width: 80vw;
      height: 70vh;
      max-width: 1440px;
    }
    margin: 0px auto;
    padding: 20px 30px;
    border-radius: 2px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.5);
    transition: all 0.3s ease;
    transform: scale(0);
    .modal-header {
      display: flex;
      justify-content: flex-end;
      img {
        display: block;
        cursor: pointer;
      }
    }
    video {
      max-width: 100%;
    }
  }
}
</style>